




















import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { recordRemainTime, ParseQuery } from "@/utils";
@Component
export default class Res extends Vue {
  @Prop()
  c!: number[];
  @Prop()
  p!: number[];
  @Prop()
  isMale!: boolean;
  getMax(arr: Array<number>): number {
    let max = 0;
    let index = 0;
    for (let i = 0; i < arr.length; i++) {
      if (max < arr[i]) {
        max = arr[i];
        index = i;
      }
    }
    return index;
  }
  url = "";
  loading = true;
  enterTime = 0;
  leaveTime = 0;
  timer = 0;
  touchStart() {
    this.timer = setTimeout(this.longpress, 500);
    return false;
  }
  touchEnd() {
    clearTimeout(this.timer);
    return false;
  }
  touchMove() {
    clearTimeout(this.timer);
    return false;
  }
  longpress() {
    this.timer = 0;
    recordRemainTime({
      id: 99,
      time: 0,
      saved: 1,
    });
  }
  change = false;
  created() {
    this.enterTime = new Date().getTime();
    let cMax = this.getMax(this.c);
    let pMax = this.getMax(this.p);
    let gender = this.isMale ? "0" : "1";
    if (cMax === 3 && pMax === 3 && !gender) this.change = true;
    if (cMax === 5) {
      gender = "";
    }
    var newImg = new Image();
    newImg.src = `https://static2.pivotstudio.cn/2021-h5-questions/poster/${cMax}${pMax}${gender}.jpg`;
    newImg.onload = () => {
      // 图片加载成功后把地址给原来的img
      let loadingtime = Math.random() * 3000 + 3000;
      this.url = newImg.src;
      setTimeout(() => {
        this.loading = false;
      }, loadingtime);
    };
    window.addEventListener("beforeunload", this.leaveHandler);
    /*
     */
  }
  async leaveHandler() {
    this.leaveTime = new Date().getTime();
    const remain = (this.leaveTime - this.enterTime) / 1000;
    const data = { id: 7, time: remain };
    window.navigator.sendBeacon("/api" + ParseQuery(data));
  }
  destroyed() {
    window.removeEventListener("beforeunload", this.leaveHandler);
  }
}
